































export default {
	data() {
		return {
			vertyText: "获取验证码",
			currentTime: 60,
			disabled: false,
			phoneNumber: '',
			password: '',
			vertyCode: '',
			interval: null,
			showcansee: false,
			focus:0
		};
	},
	computed: {
		correctPhone() {
			return this.$utils.checkMobile(this.phoneNumber);
		},
		imageurl() {
			return this.showcansee ? '../../../static/img/viewicon.png' : '../../../static/img/viewnoicon.png'
		},
		inputType() {
			return this.showcansee ? 'text' : 'password'
		}

	},
	methods: {
		userFoucs:function(){
			this.focus = 0
		},
		vertyFoucs:function(){
			this.focus = 1
		},
		passFoucs:function(){
			this.focus = 2
		},
		confirm: function() {
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			if (this.vertyCode == '') {
				uni.showToast({
					title: '请输入验证码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			if (this.password == '') {
				uni.showToast({
					title: '请输入密码',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl + 'user_getpassword',
				method: 'GET',
				data: {
					code: this.vertyCode,
					mobile: this.phoneNumber,
					password: this.password
				},
				success: res => {
					console.log(res);
					uni.showToast({
						title: res.msg,
						mask: false,
						duration: 1500,
						icon:'none'
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		seePass: function() {
			this.showcansee = !this.showcansee
		},
		getPhoneNumber: function(e) {
			this.phoneNumber = e.target.value;
			if (this.correctPhone) {
				this.piccode = this.$imageUrl + 'verify';
			}
		},
		getInputCode: function(e) {
			this.vertyCode = e.target.value;
		},
		getPassWord: function(e) {
			this.password = e.target.value;
		},
		getVertryCode: function() {
			var that = this;
			console.log("this.$webUrl==" + this.$webUrl);
			if (!this.correctPhone) {
				uni.showToast({
					title: '请输入正确手机号',
					mask: false,
					duration: 1500,
					icon:'none'
				});
				return;
			}
			this.$utils.tRequest({
				url: this.$webUrl + "user_verify_smscode",
				data: {
					mobile: this.phoneNumber
				},
				success: res => {
					that.AppSendVerifyCode(res);
				},
				fail: () => {

				},
				complete: () => {

				}
			})
		},
		AppSendVerifyCode(res) {
			console.log(JSON.stringify(res));
			var that = this;
			if (res.code == '0') {
				var currentTime = that.currentTime;
				this.interval = setInterval(function() {
					this.disabled = true
					currentTime--;
					that.vertyText = currentTime + '秒'
					if (currentTime <= 0) {
						clearInterval(this.interval)
						that.vertyText = '重新发送',
							that.currentTime = 60,
							that.disabled = false
					}

				}, 1000)
			} else {
				uni.showModal({
					title: '提示',
					content: res.msg,
					showCancel: false,
					cancelText: '',
					confirmText: '知道了',
					success: res => {},
					fail: () => {},
					complete: () => {}
				});
			}
		}
	}
}
